
@-webkit-keyframes flash {
0% {
      z-index: 100;
      opacity: 0;
}
50% {
      opacity: 1;
}
100% {
      opacity: 0;
      z-index: -1;
}
}
@keyframes flash {
0% {
      z-index: 100;
      opacity: 0;
}
50% {
      opacity: 1;
}
100% {
      opacity: 0;
      z-index: -1;
}
}
.flash-screen{
  background-color: #ffbc00;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: -1;
}
.flash-screen--in{
  opacity: 1;
  -webkit-animation: flash .3s ease-in;
          animation: flash .3s ease-in;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}























































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































